import React, { useState, useEffect } from 'react'
import { Paper, Box, Tooltip } from '@material-ui/core'
import MaterialTable, { MTableToolbar } from 'material-table'
import StartEndDatePicker from '../datePicker/StartEndDatePicker'
import clsx from 'clsx'
import produce from 'immer'

import './table.scss'

const Table = ({ term = false, termColumn = '', scrollable = false, components = {}, label = '개', options = {}, localization = {}, isAddToolbar = false, AddToolbar = {}, AddToolbarProps = {}, data = [], ...props }) => {
  const [searched, setSearched] = useState([])
  const [filter, setFilter] = useState({
    start: null,
    end: null
  })

  const handleChangeDate = (start, end) => {
    if (props.setDate) {
      props.setDate({ start, end })
    }
    setFilter({ start, end })
  }

  useEffect(() => {
    Array.isArray(data) && (
      setSearched(data.filter(item => (
        (!filter.start || filter.start <= new Date(item[termColumn]))
        && (!filter.end || filter.end >= new Date(item[termColumn]))
      )))
    )
  }, [data, filter.start, filter.end])

  return (
    <MaterialTable
      data={searched}
      components={{
        Container: props => (
          <Paper {...props} className={clsx('custom_table', scrollable && 'scrollable')} />
        ),
        Toolbar: props => (
          <div className="toolbar">
            <Box display="flex" alignItems="center">
              <MTableToolbar {...props} />
              
              {isAddToolbar && (
                <AddToolbar {...AddToolbarProps}></AddToolbar>
              )}
            </Box>

            {term && (
              <StartEndDatePicker
                start={filter.start}
                end={filter.end}
                onChange={handleChangeDate}
              />
            )}
          </div>
        ),
        ...components
      }}
      localization={{
        body: {
          emptyDataSourceMessage: '검색된 항목이 없습니다.',
          filterRow: {
            filterTooltip: '필터'
          },
          editRow: {
            saveTooltip: '확인',
            cancelTooltip: '취소',
            deleteText: '정말로 이 항목을 삭제하시겠습니까?'
          },
          addTooltip: '추가',
          deleteTooltip: '삭제',
          editTooltip: '수정'
        },
        grouping: {
          placeholder: '이쪽에 제목을 드래그해 그룹화'
        },
        toolbar: {
          searchTooltip: '검색',
          searchPlaceholder: '검색',
          exportTitle: '엑셀 다운로드',
          exportName: 'CSV 다운로드',
          showColumnsTitle: '항목',
          addRemoveColumns: '항목 추가 또는 삭제'
        },
        pagination: {
          labelRowsSelect: `${label}씩`,
          firstTooltip: '첫번째 페이지',
          previousTooltip: '이전 페이지',
          nextTooltip: '다음 페이지',
          lastTooltip: '마지막 페이지',
          labelDisplayedRows: `{count}${label} 중 {from}~{to}${label}`
        },
        ...localization
      }}
      options={{
        draggable: false,
        search: false,
        doubleHorizontalScroll: true,
        columnsButton: true,
        pageSize: 100,
        pageSizeOptions: [100, 50, 20],
        ...options
      }}
      {...props}
    />
  )
}

export default Table