import React, { useState, useEffect } from 'react'

import API from '../api/Api'
import moment from 'moment'

import DefaultTemplate from '../components/template/DefaultTemplate'
import Table from '../components/table/Table'

const MemberStatistics = () => {
  const [state, setState] = useState({
    start: (new moment()).subtract(7, 'days').format('YYYY-MM-DD'),
    end: (new moment()).format('YYYY-MM-DD')
  })
  const [data, setData] = useState([])

  const columns = [
    {
      field: 'number',
      title: '번호'
    },
    {
      field: 'regDt',
      title: '일자'
    },
    {
      field: 'allCnt',
      title: '총 회원수'
    },
    {
      field: 'join',
      title: '가입회원수'
    },
    {
      field: 'exit',
      title: '탈퇴회원수'
    },
    {
      field: 'guest',
      title: '비회원가입수'
    }
  ]

  const init = async () => {
    const res = await API.getSignUpData({
      startDate: (new moment(state.start)).format('YYYY-MM-DD'),
      endDate: (new moment(state.end)).format('YYYY-MM-DD'),
      group: 'day'
    })

    setData(res.map((x, i) => ({
      number: i+1,
      ...x
    })))
  }

  useEffect(() => {
    init()
  }, [state])

  return (
    <DefaultTemplate>
      <Table
        term
        termColumn="regDt"
        title="회원 통계"
        columns={columns}
        data={data}
        date={state}
        setDate={setState}
        options={{
          filtering: true,
          exportButton: true
        }}
      />
    </DefaultTemplate>
  )
}

export default MemberStatistics