import React, { useState, useEffect } from 'react'
import { format } from 'date-fns'

import API from '../api/Api'

import DefaultTemplate from '../components/template/DefaultTemplate'
import Table from '../components/table/Table'

const PostingStatistics = () => {
  const [data, setData] = useState([])

  const columns = [
    {
      field: 'idx',
      title: '번호'
    },
    {
      field: 'reg_dt',
      title: '시간',
      render: rowData => format(new Date(rowData.reg_dt), 'yyyy-MM-dd')
    },
    {
      field: 'org_name',
      title: '기관명'
    },
    {
      field: 'dept_name',
      title: '부서명'
    },
    {
      field: 'reg_cnt',
      title: '접수 건수'
    },
    {
      field: 'done_app_cnt',
      title: 'APP 발송건수'
    },
    {
      field: 'done_mms_cnt',
      title: 'MMS 발송건수'
    },
    {
      field: 'result_mms_fail_cnt',
      title: 'MMS 발송실패 건수'
    },
    {
      field: 'done_dm_cnt',
      title: 'DM 발송건수'
    },
    {
      field: 'deny_cnt',
      title: '차단건수'
    }
  ]

  const init = async () => {
    const res = await API.getPostStatistics()

    setData(res)
  }

  useEffect(() => {
    init()
  }, [])

  return (
    <DefaultTemplate>
      <Table
        scrollable
        term
        termColumn="reg_dt"
        title="우편물 발송 통계"
        columns={columns}
        data={data}
        options={{
          filtering: true,
          exportButton: true
        }}
      />
    </DefaultTemplate>
  )
}

export default PostingStatistics