import React, { useState, useEffect } from 'react'
import { Box, TextField, Paper, Button, CircularProgress, Typography } from '@material-ui/core'
import MaterialTable from 'material-table'
import { format } from 'date-fns'
import producer from 'immer'

import '../components/queryTrace/queryTrace.scss'
import API from '../api/Api'

import DefaultTemplate from '../components/template/DefaultTemplate'
import StartEndDatePicker from '../components/datePicker/StartEndDatePicker'
import Dialog from '../components/dialog/Dialog'
import Table from '../components/table/Table'

const delay = 150
let timeoutHandle

const DISP_CLASS = {
  0: '차단',
  1: '포스톡',
  2: 'MMS',
  4: '실물우편',
  6: '실물우편',
  8: 'EMAIL',
  'auto': 'AUTO'
}

const DISP_STATUS = {
  waiting: '발송대기',
  ing: '발송처리중',
  done: '발송완료'
}

const DM_TYPE = {
  registered: '등기 우편',
  general: '일반 우편'
}

const queryTraceInfoColumns = [
  {
    field: 'regino',
    title: '등기번호'
  },
  {
    title: '보내는분/발송날짜',
    render: rowData => (
      <>
        {rowData.sendnm}
        <br />
        {rowData.itemlist[0].sortingdate}
      </>
    )
  },
  {
    title: '받는분/수신날짜',
    render: rowData => (
      <>
        {rowData.recevnm}
        <br />
        {rowData.eventymd}
      </>
    )
  },
  {
    field: 'mailkindnm',
    title: '취급구분'
  },
  {
    title: '배달결과',
    render: rowData => (
      <>
        {rowData.itemlist[rowData.itemlist.length - 1].tracestatus}
        {rowData.itemlist[rowData.itemlist.length - 1].nondelivreasnnm && (
          <>
            <br />
            사유: <span style={{ color: '#ef5350' }}>{rowData.itemlist[rowData.itemlist.length - 1].nondelivreasnnm}</span>
          </>
        )}
      </>
    )
  },
]

const queryTraceColumns = [
  {
    field: 'sortingdate',
    title: '날짜'
  },
  {
    field: 'eventhms',
    title: '시간'
  },
  {
    field: 'eventregiponm',
    title: '발생국'
  },
  {
    field: 'tracestatus',
    title: '처리현황',
    render: rowData => (
      <>
        {rowData.tracestatus}
        {rowData.nondelivreasnnm && (
          <>
            <br/>
            사유: <span style={{ color: '#ef5350' }}>{rowData.nondelivreasnnm}</span>
          </>
        )}
      </>
    )
  }
]


const PostingLookup = () => {
  const signUserType = localStorage.getItem('type')

  const tableRef = React.useRef()

  const [dialog, setDialog] = useState({
    open: false,
    query: '',
    data: false
  })

  const [filter, setFilter] = useState({
    org_code: '',
    dept_code: '',
    start: new Date('2019-01-01'),
    end: new Date()
  })

  const columns = [
    {
      field: 'org_name',
      title: '발송기관'
    },
    {
      field: 'dept_name',
      title: '발송부서'
    },
    {
      field: 'reg_dt',
      title: '분석날짜',
      render: rowData => format(new Date(rowData.reg_dt), 'yyyy-MM-dd HH:mm:dd')
    },
    {
      field: 'name',
      title: '수신자 이름'
    },
    {
      field: 'template_code',
      title: '서식 코드'
    },
    {
      field: 'doc_title',
      title: '문서 제목'
    },
    {
      field: 'is_readed',
      title: '우편열람여부',
      lookup: {
        '0': '미열람',
        '1': '열람'
      }
    },
    {
      field: 'dm_type',
      title: '우편 구분'
    },
    {
      field: 'dm_reg_num',
      title: '등기 번호',
      render: rowData => (
        <Button color="primary" onClick={() => handleOpenDialog(rowData.dm_reg_num)}>{rowData.dm_reg_num}</Button>
      )
    },
    {
      field: 'disp_class',
      title: '발송종류'
    },
    {
      field: 'disp_status',
      title: '발송상태'
    },
    {
      field: 'request_id',
      title: '발송 신청번호'
    },
    {
      field: 'groupId',
      title: '묶음발송 신청번호'
    },
    {
      field: 'read_at',
      title: '우편열람시간',
      render: rowData => rowData.read_at ? format(new Date(rowData.read_at), 'yyyy-MM-dd HH:mm:dd') : ''
    }
  ]

  const init = async () => {
    if (signUserType === 'org') {
      const userId = localStorage.getItem('userId')
      const [user] = await API.getAccount('org', userId)
      const { org_code, dept_code } = await API.searchOrg('name', user.org_name, user.dept_name)

      setFilter({
        ...filter,
        org_code,
        dept_code
      })
    }
  }

  const getPostingLookup = async(page, page_size) => {
    const { hits } = await API.getPostSend({
      ...filter,
      page_from: page * page_size,
      page_size
    })

    if (!hits || hits.hits.length < 1) {
      return {
        data: [],
        page: page,
        totalCount: 0
      }
    }

    const { org_name, dept_name } = await API.searchOrg('code', filter.org_code, filter.dept_code)

    const data = hits.hits.map(item => ({
      org_name,
      dept_name,
      name: item._source.binding.reserved.additional.receiver.name,
      dm_type: item._source.binding.reserved.essential.dispatching.dm ? (DM_TYPE[item._source.binding.reserved.essential.dispatching.dm['type']] || '') : '',
      dm_reg_num: item._source.binding.reserved.essential.dispatching.dm ? item._source.binding.reserved.essential.dispatching.dm['reg-num'] : '',
      reg_dt: item._source.status.time['@analysised'],
      read_at: item._source.status.time['@read'],
      template_code: item._source.binding.reserved.essential.template.code,
      doc_title: item._source.binding.reserved.essential.template.title,
      disp_class: DISP_CLASS[item._source.binding.reserved.essential.dispatching.class],
      disp_status: DISP_STATUS[item._source.status.dispatching],
      request_id: item._source.binding.reserved.essential.search['request-id'],
      groupId: item._source.binding.reserved.essential.search['group-by'],
      is_readed: String(item._source.status.read)
    }))

    return {
      data,
      page: page,
      totalCount: hits.total
    }
  }

  const handleChangeFilterValue = (key, value) => {
    setFilter(producer(draft => {
      draft[key] = value
    }))
  }

  const handleChangeDate = (start, end) => {
    setFilter({
      ...filter,
      start,
      end
    })
  }

  const queryChange = () => {
    clearTimeout(timeoutHandle)
  
    timeoutHandle = setTimeout(() => {
      tableRef.current && tableRef.current.onQueryChange()
    }, delay)
  }

  const handleOpenDialog = (query) => {
    setDialog({
      ...dialog,
      open: true,
      data: false,
      query
    })
  }

  const handleCloseDialog = () => {
    setDialog({
      ...dialog,
      open: false
    })
  }

  const queryTrace = async () => {
    const { trace } = await API.queryTrace(dialog.query)
    let data = null

    if (trace) {
      data = Object.keys(trace).reduce((acc, key) => ({
        ...acc,
        [key]: trace[key][0]
      }), {})

      data.itemlist = data.itemlist.item.map(item => Object.keys(item).reduce((acc, key) => ({
        ...acc,
        [key]: item[key][0]
      }), {}))
    }

    setDialog({
      ...dialog,
      data
    })
  }

  useEffect(() => {
    init()
  }, [])

  useEffect(() => {
    queryChange()
  }, [filter])

  useEffect(() => {
    if (dialog.open) {
      setTimeout(() => {
        queryTrace()
      }, 1000)
    }
  }, [dialog.open])


  return (
    <DefaultTemplate>

      <Dialog open={dialog.open} onClose={handleCloseDialog} message={{ title: '등기 번호 조회' }} maxWidth="md" className="queryTraceDialog" fullWidth>
        {dialog.data === false && (
          <div className="empty">
            <CircularProgress />
          </div>
        )}

        {dialog.data === null && (
          <div className="empty">
            <Typography>검색 결과가 없습니다</Typography>
          </div>
        )}

        {(dialog.data) instanceof Object && (
          <>
            <Table
              title="기본 정보"
              columns={queryTraceInfoColumns}
              data={[ dialog.data ]}
              options={{
                paging: false,
                columnsButton: false
              }}
            />

            <br />
            <br />

            <Table
              title="추적 정보"
              columns={queryTraceColumns}
              data={dialog.data.itemlist}
              options={{
                paging: false,
                columnsButton: false
              }}
            />
          </>
        )}
      </Dialog>

      <Box marginY={2}>
        <Paper>
          <Box display="flex" alignItems="center" justifyContent="space-between" padding={2}>
            <Box display="flex" alignItems="center">
              {signUserType === 'system' && (
                <>
                  <TextField
                    variant="outlined"
                    label="기관코드"
                    value={filter.org_code}
                    onChange={(e) => {
                      handleChangeFilterValue('org_code', e.target.value)
                    }}
                    style={{ marginRight: 12 }}
                  />

                  <TextField
                    variant="outlined"
                    label="부서코드"
                    value={filter.dept_code}
                    onChange={(e) => {
                      handleChangeFilterValue('dept_code', e.target.value)
                    }}
                  />
                </> 
              )}
            </Box>

            <StartEndDatePicker
              start={filter.start}
              end={filter.end}
              onChange={handleChangeDate}
            />
          </Box>
        </Paper>
      </Box>

      <MaterialTable
        title="우편물 발송 조회"
        tableRef={tableRef}
        columns={columns}
        data={query =>
          new Promise((res, rej) => {
            getPostingLookup(query.page, query.pageSize)
              .then(result => {
                res(result)
              })
          })
        }
        options={{
          draggable: false,
          search: false,
          doubleHorizontalScroll: true,
          columnsButton: true,
          filtering: true,
          exportButton: true,
          pageSize: 100,
          pageSizeOptions: [100, 50, 20],
        }}
        components={{
          Container: props => (
            <Paper {...props} className="custom_table scrollable" />
          )
        }}
        localization={{
          body: {
            emptyDataSourceMessage: '검색된 항목이 없습니다.',
            filterRow: {
              filterTooltip: '필터'
            },
            editRow: {
              saveTooltip: '확인',
              cancelTooltip: '취소',
              deleteText: '정말로 이 항목을 삭제하시겠습니까?'
            },
            addTooltip: '추가',
            deleteTooltip: '삭제',
            editTooltip: '수정'
          },
          grouping: {
            placeholder: '이쪽에 제목을 드래그해 그룹화'
          },
          toolbar: {
            searchTooltip: '검색',
            searchPlaceholder: '검색',
            exportTitle: '엑셀 다운로드',
            exportName: 'CSV 다운로드',
            showColumnsTitle: '항목',
            addRemoveColumns: '항목 추가 또는 삭제'
          },
          pagination: {
            labelRowsSelect: `개씩`,
            firstTooltip: '첫번째 페이지',
            previousTooltip: '이전 페이지',
            nextTooltip: '다음 페이지',
            lastTooltip: '마지막 페이지',
            labelDisplayedRows: `{count}개 중 {from}~{to}개`
          }
        }}
      />
    </DefaultTemplate>
  )
}

export default PostingLookup