import React, { useState, useEffect } from 'react'
import { format } from 'date-fns'

import API from '../api/Api'

import { NavLink, withRouter } from 'react-router-dom'
import DefaultTemplate from '../components/template/DefaultTemplate'
import Table from '../components/table/Table'


const OrgAccount = ({ history }) => {

  const [data, setData] = useState([])

  const columns = [
    {
      field: 'number',
      title: '번호',
      render: rowData => {
        return rowData.tableData.id + 1
      }
    },
    {
      field: 'name',
      title: '이름'
    },
    {
      field: 'hp',
      title: '휴대폰번호'
    },
    {
      field: 'p_code',
      title: 'pcode'
    },
    {
      field: 'entry_dt',
      title: '등록일',
      render: rowData => format(new Date(rowData.entry_dt), 'yyyy-MM-dd HH:mm:dd')
    },
    // {
    //   field: 'idk',
    //   title: '구분'
    // },
    // {
    //   field: 'disp_class',
    //   title: '우편물 수령 방법'
    // }
  ]

  const init = async () => {
    const res = await API.getAccounts('member')

    setData(res)
  }

  useEffect(() => {
    init()
  }, [])
  
  const handleRowClick = (e, rowData) => {
    history.push(`/member/account/view/${rowData.uuid}`)
    
    return false
  }

  return (
    <>
      <DefaultTemplate>
        <Table
          title="회원 계정 관리"
          columns={columns}
          data={data}
          onRowClick={handleRowClick}
          options={{
            filtering: true,
            exportButton: true
          }}
        />
      </DefaultTemplate>
    </>
  )
}

export default withRouter(OrgAccount)