import React from 'react'
import { NavLink } from 'react-router-dom'
import { List, ListItem, ListItemText, Divider } from '@material-ui/core'
import { match as createMatch } from 'path-to-regexp2'

import routes from '../../configs/routes'
import logo from '../../images/logo.png'

import './nav.scss'

const isSystem = window.localStorage.getItem('type') === 'system'


const Link = ({ path, children, parent, isActive }) => {
  const className = parent ? 'parent' : 'children'

  const handleIsActive = (match, location) => {
    return isActive ? !!createMatch(isActive)(location.pathname) : !!match
  }

  return path === undefined ? (
    <div className={className}>
      {children}
    </div>
  ) : (
      <NavLink exact to={path} className={className} isActive={handleIsActive}>
        {children}
      </NavLink>
    )
}

const LinkListItem = ({ path, label, hidden, parent, isActive }) => {
  return !hidden && (
    <Link path={path} parent={parent} isActive={isActive}>
      <ListItem button={path !== undefined} dense>
        <ListItemText primary={label} />
      </ListItem>
    </Link>
  )
}

const Nav = () => (
  <nav className="nav">
    <div className="logo">
      <img src={logo} alt="logo" />
    </div>

    <List>
      {routes.map((item, i) => !item.hidden && (!item.system || (item.system && isSystem)) && (
        <div key={i}>
          <LinkListItem {...item} parent />

          {item.children && item.children.map((item2, i) => (!item2.system || (item2.system && isSystem)) && (
            <LinkListItem {...item2} key={i} />
          ))}

          <Divider />
        </div>
      ))}
    </List>
  </nav>
)

export default Nav